<template>
  <div>
    <h2>Shopping Cart</h2>
    <ul>
      <li v-for="item in cart" :key="item.id">
        {{ item.subject }} - £{{ item.price }} (Quantity: {{ item.quantity }})
        <button @click="$emit('remove-from-cart', item.id)">Remove</button>
      </li>
    </ul>
    <p v-if="cart.length === 0">Your cart is empty.</p>

    <!-- Name and Phone Input -->
    <div v-if="cart.length > 0">
      <label>
        Name:
        <input
          type="text"
          v-model="localName"
          @input="$emit('update:name', localName)"
          placeholder="Enter your name"
        />
      </label>
      <label>
        Phone:
        <input
          type="text"
          v-model="localPhone"
          @input="$emit('update:phone', localPhone)"
          placeholder="Enter your phone number"
        />
      </label>
      <button @click="checkout">Checkout</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Cart",
  props: {
    cart: {
      type: Array,
      required: true,
    },
    name: {
      type: String,
      required: true,
    },
    phone: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      localName: this.name, // Local copy of the name
      localPhone: this.phone, // Local copy of the phone
    };
  },
  methods: {
    checkout() {
      this.$emit("checkout"); // Emit checkout event to parent
    },
  },
};
</script>

