ul {
  list-style: none;
  padding: 0;
}
li {
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
button {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  cursor: pointer;
}
button:hover {
  background-color: #c82333;
}
input {
  display: block;
  margin: 0.5rem 0;
  padding: 0.5rem;
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 5px;
}
</style>
