<template>
  <div id="app">
    <header>
      <h1>School Enrollment App</h1>
    </header>

    <main>
      <LessonList
        :lessons="lessons"
        @add-to-cart="addToCart"
      />
      <Cart
        :cart="cart"
        :name="name"
        :phone="phone"
        @remove-from-cart="removeFromCart"
        @update:name="updateName"
        @update:phone="updatePhone"
        @checkout="handleCheckout"
      />
    </main>
  </div>
</template>

<script>
import LessonList from "./components/LessonList.vue";
import Cart from "./components/Cart.vue";

export default {
  name: "App",
  components: { LessonList, Cart },
  data() {
    return {
      lessons: [
        { id: 1, subject: "Art", location: "London", price: 9, spaces: 6 },
        { id: 2, subject: "English", location: "Bristol", price: 8, spaces: 10 },
        { id: 3, subject: "Math", location: "London", price: 10, spaces: 5 },
        { id: 4, subject: "Music", location: "Oxford", price: 7, spaces: 8 },
        { id: 5, subject: "Science", location: "New York", price: 15, spaces: 2 },
      ],
      cart: [],
      name: "",
      phone: "",
    };
  },
  methods: {
    addToCart(lesson) {
      const existingLesson = this.cart.find((item) => item.id === lesson.id);
      if (existingLesson) {
        existingLesson.quantity += 1;
      } else {
        this.cart.push({ ...lesson, quantity: 1 });
      }
    },
    removeFromCart(lessonId) {
      this.cart = this.cart.filter((item) => item.id !== lessonId);
    },
    updateName(value) {
      this.name = value;
    },
    updatePhone(value) {
      this.phone = value;
    },
    handleCheckout() {
      if (!this.name || !this.phone) {
        alert("Please provide your name and phone number to checkout.");
        return;
      }
      alert(`Thank you, ${this.name}! Your enrollment has been confirmed.`);
      this.cart = [];
      this.name = "";
      this.phone = "";
    },
  },
};
</script>

