<template>
  <div>
    <h2>Available Lessons</h2>
    <div class="sort-options">
      <label>Sort by:</label>
      <select v-model="sortKey">
        <option value="subject">Subject</option>
        <option value="location">Location</option>
        <option value="price">Price</option>
        <option value="spaces">Availability</option>
      </select>
      <label>
        <input type="radio" value="asc" v-model="sortOrder" />
        Ascending
      </label>
      <label>
        <input type="radio" value="desc" v-model="sortOrder" />
        Descending
      </label>
    </div>
    <div class="lesson-cards">
      <div
        v-for="lesson in sortedLessons"
        :key="lesson.id"
        class="lesson-card"
      >
        <h3>Subject: {{ lesson.subject }}</h3>
        <p>Location: {{ lesson.location }}</p>
        <p>Price: £{{ lesson.price }}</p>
        <p>Spaces: {{ lesson.spaces }}</p>
        <button @click="$emit('add-to-cart', lesson)">Add to Cart</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "LessonList",
  props: {
    lessons: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      sortKey: "subject",
      sortOrder: "asc",
    };
  },
  computed: {
    sortedLessons() {
      const sorted = [...this.lessons].sort((a, b) => {
        let comparison = 0;

        if (a[this.sortKey] < b[this.sortKey]) comparison = -1;
        else if (a[this.sortKey] > b[this.sortKey]) comparison = 1;

        return this.sortOrder === "asc" ? comparison : -comparison;
      });

      return sorted;
    },
  },
};
</script>

