.sort-options {
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}
.lesson-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
}
.lesson-card {
  border: 1px solid #ccc;
  padding: 1rem;
  border-radius: 8px;
  text-align: center;
}
